  {"編號":"3","校名":"私立／國立復旦大學","分校":"b.滬校：","時間":"1938/02/01","遷移地名":"上海英/法租界","經度":"121.28","緯度":"31.13","附註":"1938年2月，復旦數百留滬學生要求李登輝校長設法上課，解決失學問題。以滬上租界之內，尚屬安全，並無任何干涉。以上好最後一課的精神，維持復旦在滬之根基。如形勢突變，則「當散即散」，亦屬易事。于是，在校友的協助下，租英租北京東路中一信托大樓的四、五樓作為臨時校舍，1938年2月23日正式上課，至1938年6月25日學期結束。\n因中一信托大樓地處鬧市，熙熙嚷嚷，不宜辦學。1938年下學期，租借法租界霞飛路（今淮海路）1726號房屋為校舍。\n吳南軒由渝來滬，商定滬校改稱復旦大學補習部。","備註":"（復旦大學校史編寫組編，《復旦大學誌》（第一卷）（上海：復旦大學出版社，1985年），頁154, 155。）"}
